#hero--form {
  display: grid;
  place-items: center;
}

#hero--form form {
	display: flex;
	flex-direction: column;
	min-width: 460px;
	border: 1px solid var(--grey);
  border-radius: 5px;
}

#hero--form input {
	min-width: 460px;
	height: 48px;
	padding-left: 15px;
	border: none;
	border-bottom: solid 1px var(--grey); 
  background: rgba(155, 155, 155, 0.15);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(1px);
  -webkit-backdrop-filter: blur(2.5px);
	font-family: "Roboto", sans-serif;
  font-size: 18px;
	font-weight: 400;
  color: var(--white);
}

#password_container {
  position: relative;
}

#password_container svg.eye {
  position: absolute;
  right: 20px;
  top: 25%;
  fill: var(--grey);
}

#password_container rect.btn {
  stroke: var(--grey);
  fill: var(--grey);
  fill-opacity: 0;
  stroke-opacity: 0;
}

#hero--form button {
  height: 60px;
	border: none;
  border-radius: 0 0 4px 4px;
  background-color: var(--blue);
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: var(--white);
}

#hero--form button:hover {
  background: #113d5e; 
}

@media screen and (min-width: 0px) and (max-width: 500px) 
{
  #hero--form form {
  min-width: initial;
  width: 90vw;
  }

  #hero--form input {
    min-width: initial;
    width: 100%;
  }
  }