nav ul {
	display: flex;
	list-style: none;
	font-family: "Roboto", sans-serif;
	color: var(--white);
	font-weight: 400;
}
nav li {
	padding-right: 50px;
}

nav li a {
	text-decoration: none;
}

nav#hamburger_nav {
	display: none;

	}



nav#desktop_nav {
	display: initial;
}

@media screen and (min-width: 0px) and (max-width: 651px) {
	/* Point at which links in desktop nav wrap beneath Stamp logo. */
	nav#desktop_nav {
		display: none;
	}

	nav#hamburger_nav {
		display: initial;
	}
}


