#hero--container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

#hero--text {
  max-width: 65ch;
  // margin-top: -20px;
  // margin-left: 105px;
  animation: slidein;
	animation-duration: 2s;
  animation-timing-function: ease-out;
}

#hero--text p:first-child {
  font-family: "Avenir Substitute", sans-serif;
  font-weight: 600;
  font-size: 72px;
  color: var(--white);
}

#hero--text p:nth-child(2) {
  margin-top: 30px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 22px;
  color: var(--white);
}

#hero--form h2,
#hero--form span {
  text-align: center;
  align-self: center;
  color: var(--white);
}

#hero--form h2 {
  opacity: 0;
  margin-bottom: 15px;
  font-family: "Avenir Substitute", sans-serif;
  text-transform: uppercase;
  animation: fadein;
	animation-duration: 4s;
  animation-delay: 4s;
  animation-fill-mode: forwards;
}

#hero--form span {
  display: block;
  color: var(--grey);
}

#hero--form span:first-of-type {
  margin-top: 15px;
  padding-bottom: 5px;
}




/* #region MEDIA QUERIES */
@media screen and (min-width: 0px) and (max-width: 575px) {

  #hero--container {
    padding-left: 10vw;
    padding-right: 10vw;
    }
  
  #hero--form span:nth-of-type(2){
    margin-bottom: 25px;
  }
}

@media screen and (min-width: 0px) and (max-width: 700px) {
  #hero--text {
    max-width: 45ch;
  }
  
  #hero--text p:first-child {
    font-size: 45px;}

    #hero--text p:nth-child(2){
      margin-bottom: 45px;
  }
}

@media screen and (min-width: 0px) and (max-width: 1000px) {
  #hero--text {
    margin-bottom: 45px;
  }

  header svg#stamp_logo {
    margin-left: 5vw;
  }
}


/* #endregion MEDIA QUERIES */


